.accordion {
  background-color: #d9e2f0;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: none;
  text-align: center;
  outline: none;
  transition: 0.5s;
}

.active{
  border: 1px solid #0d266d;
}

.accordion:hover {
  background-color: #b4ccf0;
}

.panel {
  display: none;
  overflow: hidden;
}
